<template>
      <div>
        <div class="aside_wrap" :class="showThe?'aside_wrap--active':''">
        <div class="aside" v-if="infoData" @click.stop="showData" v-loading="loading">
          <span class="aside-title"></span>
          <div class="aside-cont t-bottom">
            <span class="aside-info"></span>
            <ul class="aside-cont">
              <li>{{infoData.product.identification}}</li>
              <li>{{infoData.product.name}}</li>
              <li>{{infoData.product.version}}</li>
              <li class="t-12">{{infoData.product.authorization}}
                <br>
              <el-button size="mini" type="primary" @click.stop="dialogVisible=true"></el-button></li>
              <li>{{infoData.product.startTime}}</li>
            </ul>
          </div>
          <div class="aside-cont t-bottom">
            <span class="aside-info"></span>
            <ul class="aside-cont">
              <li>{{infoData.environment.system}}</li>
              <li>JDK{{infoData.environment.jdk}}</li>
            </ul>
          </div>
          <div class="aside-cont">
            <span class="aside-info"></span>
            <ul class="aside-cont">
              <li>{{infoData.database.databaseType}}</li>
              <li>{{infoData.database.url}}</li>
              <li>{{infoData.database.port}}</li>
              <li>{{infoData.database.databaseName}}</li>
            </ul>
          </div>
        </div>
      </div>
      <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="40%"
          :close-on-click-modal="false"
          :before-close="handleClose">
          <div class="t-12 t-font">
            <p style="margin-bottom:5px"><i class="el-icon-warning" style="color:#FFA000" ></i>  </p>
            <p class="t-padding"> </p>
            <p class="t-padding"> </p>
            <p class="t-padding"> </p>
            <p class="t-padding"> JEECMS   <a style="color:#66B1FF" href="http://www.jeecms.com/" target="_blank"></a></p>
            <el-input maxlength="50" :autofocus="true" ref="first" v-model="attrValue" placeholder="" type="textarea" ></el-input>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button size="mini" @click.stop="handleClose"> </el-button>
            <el-button size="mini" :disabled="attrValue===''" type="primary" @click.stop="getRole"> </el-button>
          </span>
        </el-dialog>
      </div>
</template>

<script>
import { productinfo, commandChange } from './../../api/global.js'
export default {
  name: 'cmsAside',
  props: {
    show: {// 
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      infoData: null,
      dialogVisible: false,
      attrValue: ''
    }
  },
  computed: {
    showThe() {
      return this.$store.state.sideBar.asideStatus
    }
  },
  methods: {
    handleClose() { // 
      this.dialogVisible = false
      this.$store.dispatch('toggleAside', false)
    },
    getRole() { // 
      var params = {
        attrValue: this.attrValue
      }
      commandChange(params).then(res => {
        if (res.code === 200) {
          this.$successMessage('')
          this.handleClose()
        } else {
          this.handleClose()
        }
      })
    },
    hideAside() { // 
      if (!this.dialogVisible) {
        this.$store.dispatch('toggleAside', false)
      }
    },
    showData() { // 
      this.$store.dispatch('toggleAside', true)
    },
    showInfo() { // 
      this.loading = true
      productinfo().then(res => {
        if (res.code === 200) {
          this.infoData = res.data
          this.loading = false
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    }
  },
  watch: {
    showThe(value) {
      if (!this.infoData) {
        this.showInfo()
      }
      if (value) {
        window.addEventListener('click', this.hideAside)
      } else {
        window.removeEventListener('click', this.hideAside)
      }
    }
  },

  created() {
  }
}
</script>

<style lang="scss" scoped>
 
  .aside_wrap{
    position: fixed;
    top: 54px;
    right: 0;
    width: 330px;
    height: 100%;
    background-color: #E9E9E9;
    z-index: 4002;
      transition: 0.25s transform;
   transform: translateX(100%);
  }
  .aside_wrap--active{
 transform: translateX(0);
  }
  .aside{
    padding: 0 20px;
    box-sizing: border-box;
    color: #666;
    width: 100%;
    height: 100%;
    text-align: left;
    line-height: 24px;
    transition: 1s;
  }
  .aside-title{
    font-size: 16px;
    line-height: 48px;
  }
  .aside-cont{
    font-size: 14px;
    padding: 5px 10px 5px;
  }
  .aside-info{
    font-size: 14px;
  }
  .t-bottom{
    border-bottom: 1px solid #ccc;
  }
  .t-12{
    font-size: 12px;
  }
  .t-font{
    color: #999;
    line-height: 16px;
    text-align: left;
    padding: 20px;
  }
  .t-padding{
    padding-left: 10px;
    padding-bottom: 10px;
  }
</style>




